header {
  background: var(--color-primary);
  padding: 30px 0;

  @include _767 {
    padding: 10px 0;

    .container {
      min-width: auto;
    }
  }

  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo {

      @include _1200 {
        width: 75px;
      }
    }

    nav {
      ul {
        margin: 0;
        display: flex;
        align-items: center;

        li {
          margin-right: 40px;

          @include _1200 {
            margin-right: 22px;
          }

          &:last-child {
            margin-right: 0;
          }

          a {
            color: var(--color-white);
            font-size: 14px;
            font-weight: bold;
            font-family: 'Panton';

            @include _1200 {
              font-size: 12px;
            }

            &:hover {
              color: var(--color-red);
            }

            &.btn-primary:hover {
              color: var(--color-white);
            }
          }
        }
      }

      @include _767 {
        position: fixed;
        width: 320px;
        background: var(--color-primary);
        left: -100%;
        transition: 0.5s cubic-bezier(0.86, 0, 0.07, 1);
        top:49px;
        padding: 15px 15px 15px 60px;
        z-index: 2500;

        &.is-open {
          left: 0;
        }

        ul {
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          text-transform: uppercase;
          font-weight: 700;

          li a {
            font-size: 18px;
          }
        }
      }
    }
  }
  @include _767 {
    position: fixed;
    top:0;
    width: 100%;
    z-index: 1500;
  }
}