/* --------- Button Default --------- */

.btn {
  border-radius: 3px;
  background: var(--color-primary);
  color: var(--color-white);
  padding: 17px 30px;
  cursor: pointer;
  outline: none;
  display: inline-block;
  border-color: transparent;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  transition: all .3s ease;

  &:hover {
    color: var(--color-white);
    background: var(--color-hover);
  }

  @include _991 {
    padding: 12px 20px;
    font-size: 13px;
    font-weight: 600;
  }
}

.btn-primary {
  text-transform: uppercase;
  background: var(--color-active);
  border-radius: 20px;
  padding: 10px 20px;
  font-weight: bold;
  color: #fff;

  &:hover {
    background: var(--color-red);
  }
}