section {
  position: relative;
}

.section-one__block {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 35px 0 0;
  position: relative;

  @include _1200 {
    padding-top: 20px;
  }

  @include _767 {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 230px;
  }

  .section-one__block-heart {
    padding-left: 25px;

    .section-one__block-desc {
      background: rgba(255,255,255, 0.9);
      border-radius:  0 0 0 20px;
      text-transform: uppercase;
      font-weight: 700;
      position: relative;
      top:-30px;
      width: 330px;
      padding: 10px;
      line-height: 27px;

      @include _767 {
        border-radius:  0 20px 0 20px;
      }
    }

    img.heart {
      position: absolute;
      top:-18px;

      @include _1200 {
        display: none;
      }
    }
  }
  .section-one__block-video {
    padding-right: 70px;

    @include _1200 {
      padding-right: 0;
    }

    .section-one__block-tablet {
      display: block;
      background: url("../img/tablet.png") no-repeat;
      width: 484px;
      height: 336px;

      @include _1200 {
        width: 320px;
        height: 223px;
        background-size: contain;
      }

      @include _767 {
        margin: 23px 0 48px 0;
      }
    }

    .section-one__block-desc-2 {
      padding: 10px;
      line-height: 27px;
    }
    .section-one__block-inter-item {
      justify-content: flex-end;
    }

    .f25 {
      font-size: 18px;
      line-height: 20px;
    }
  }

  .section-one__block-inter-item {
    display: flex;
    align-items: center;
    cursor: pointer;

    &:first-child {
      padding-left: 25px;
    }
    &:last-child {
      padding-right: 70px;
    }
    .section-one__block-inter-item-text {
      display: flex;
      font-size: 15px;
      padding-left: 15px;
      line-height: 25px;
      font-weight: 700;
      flex-direction: column;
      text-decoration: underline;
    }

    &:hover .section-one__block-inter-item-text {
      text-decoration: none;
    }
  }
}


