        z-index: 10;
        border-radius: 15px;
        font-size: 15px;
        padding: 10px 10px 10px 44px;
        border:4px solid var(--color-primary);
        background: var(--color-white);
        font-weight: 600;

        @include _1200 {
          width: 100%;
          margin-bottom: 20px;
        }

        &:first-child {
          margin-bottom: 20px;
        }
        &:nth-child(2) {
