          line-height: 16px;
        }

        .section-2__content-block-desc {
          line-height: 18px;
        }
        .section-2__content-block-btn {
          border:1px solid var(--color-red);
          border-radius: 30px;
          font-size: 13px;
          font-weight: normal;
          color: #9ba5b6;
          padding: 0 15px;
          transition: all .3s ease;
          cursor: pointer;

          &:hover {
            background: var(--color-red);
            color: #fff;
            border-color:  var(--color-red);
          }
        }
      }
    }
