.burger-icon{
  display:none;
  flex-direction:column;
  width:30px;
  cursor:pointer;
  z-index: 1000;

  @include _767 {
    width:26px;
  }

  @include _991 {
    display: flex;
    margin-left: 15px;
  }
}

.burger-icon span{
  background: #fff;
  height:3px;
  margin: 3px 0;
  transition: .4s  cubic-bezier(0.68, -0.6, 0.32, 1.6);

  @include _767 {
    height:2px;
  }

}


.burger-icon span:nth-of-type(1){
  width:50%;

}

.burger-icon span:nth-of-type(2){
  width:100%;
}


.burger-icon span:nth-of-type(3){
  width:75%;

}



.burger-icon.active span:nth-of-type(1){
  transform-origin:bottom;
  transform:rotatez(45deg) translate(3px,0px);

  @include _767 {
    transform:rotatez(45deg) translate(3px,1px);
  }
}


.burger-icon.active span:nth-of-type(2){

  transform-origin:top;
  transform:rotatez(-45deg)
}


.burger-icon.active span:nth-of-type(3){

  transform-origin:bottom;
  width:50%;
  transform: translate(13px,-5px) rotatez(45deg);

  @include _767 {
    transform: translate(11px,-4px) rotatez(45deg);
  }

}
